/* markers */
.marker,
.questionMark {
  height: 30px;
  width: 30px !important;
}
.marker {
  position: relative;
}
.shadow {
  background: url(data:image/png;base64,);
  background-size: 35px 16px;
  width: 50px;
  height: 50px;
  position: absolute;
  background-repeat: no-repeat;
  top: 15px;
  z-index: 1;
  filter: blur(2px);
  pointer-events: none;
}
.markerWrap {
  position: relative;
  height: 30px;
  width: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.markerWrapMoused:hover > div:first-child > svg > path {
  animation-name: moused-marker-shadow;
  animation-duration: 1.5s;
  animation-fill-mode: forwards;
  animation-timing-function: linear;
}
.markerWrapMoused > div:first-child > svg > text {
  pointer-events: none;
}
.markerWrap > div {
  position: absolute;
  font-size: 0.7rem;
}
.questionMark {
  opacity: 0.1;
}
.mousedMarker path {
  animation-name: moused-marker-shadow;
  animation-duration: 1.5s;
  animation-fill-mode: forwards;
  animation-timing-function: linear;
}
.mousedMarkerWrapInifinite path {
  animation-name: moused-marker-shadow-hover;
  animation-duration: 1.5s;
  animation-fill-mode: forwards;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
}
@keyframes moused-marker-shadow {
  25% {
    fill: rgb(254, 227, 145);
  }
  50% {
    fill: rgb(254, 196, 79);
  }
  100% {
    fill: rgb(252, 186, 3);
  }
}
@keyframes moused-marker-shadow-hover {
  20% {
    fill: rgb(254, 227, 145);
  }
  40% {
    fill: rgb(254, 196, 79);
  }
  60% {
    fill: rgb(252, 186, 3);
  }
  80% {
    fill: rgb(254, 196, 79);
  }
  100% {
    fill: rgb(254, 227, 145);
  }
}
