.compareToolIcon {
  height: 20px;
  width: 20px !important;
  color: black;
}
.gazHeader {
  margin: 5px;
}
.gazHeader > div {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1000;
  cursor: pointer;
}
.gazHeader > p {
  font-weight: bold;
}
.gazHeader svg {
  margin: 1px;
  height: 10px;
  width: 10px !important;
  min-width: 10px !important;
  min-height: 10px !important;
}
.cellValue {
  overflow: auto;
  margin-bottom: 5px;
  scrollbar-width: thin;
}
.originalAttribute {
  color: grey;
}
.resizer {
  position: relative;
  float: right;
  height: 100%;
  width: 2px;
  cursor: ew-resize;
  background-color: rgba(0, 0, 0, 0.1);
}
.columnsWrapper {
  display: grid;
  height: 100%;
  box-shadow: rgba(0, 0, 0, 0.3) 0px -15px 10px -15px inset;
}
.columnsWrapper > div:first-child {
  display: flex;
}
.isCompareToolSwitcherIfMatchingIsOn {
  top: 100px;
  border-top: 1px solid rgb(0, 0, 0, 0.1);
  z-index: 3;
}
.wrapper {
  z-index: -1;
}
